@charset "utf-8";
@import "vars";
@import '../../node_modules/bulma/sass/utilities/_all';

.title{
  color: $tonico;
}

.subtitle {
  padding: 0.5em;
  color: #FFBE74;
  margin-bottom: .5rem !important;
}

.background {
  color: #bfbfbf;
  background-color: rgba($white, 0.2);
  padding: 0.5em;
  &:first-of-type{
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
  }
  &:last-of-type{
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
  }
}

.cool-bg {
  background-image: linear-gradient(-90deg, rgba(255,255,255,0.3) 0%, rgba(0,0,0,0) 70%);
  &:nth-child(even) {
    background-image: linear-gradient(-90deg, rgba(255,255,255,0.25) 0%, rgba(0,0,0,0) 70%);
  }
}

.model-container {
  width: 100%;
  height: 150px;
  @include until($tablet){
    height: 300px;
  }
}

model-viewer {
  --poster-color: transparent;
  width: 100%;
  height: 100%;
}

.glide{
  display: flex;
  li {
    margin:auto;
  }
}

.empresa{
  picture{
    display: flex;
  }
  img {
    margin: auto;
    max-height: 160px;
  }
}

p {
  text-align: justify;
}

.grow-p {
  display: flex;
  flex-direction: column;
  p {
    flex-grow: 1;
  }
}

.background picture {
  display: block;
  img {
    width: 100%;
  }
  filter: grayscale(1);
  transition: .5s;
}

.background:hover picture {
  filter: grayscale(0);
}